.card {
    margin-right: 15px;
    margin: 1rem;
    border-style:groove;
    border-color: white;
    min-width: 175px;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    background-color: rgba(28, 9, 32, 0.288);
    color:white;
    text-shadow:
          -1px -1px 0 #000,
          1px -1px 0 #000,
          -1px 1px 0 #000,
          1px 1px 0 #000;
  }

  .card li {
    flex-direction: column;
  }

  .card button{
    margin : auto;
    background-color: rgba(28, 9, 32, 0.288);
    color:white;
    text-shadow:
          -1px -1px 0 #000,
          1px -1px 0 #000,
          -1px 1px 0 #000,
          1px 1px 0 #000;
          border-style:groove;
          border-color: white;
  }
  

  .apt-form button {
      margin-top: 25px;
      
  }

  .apt-div {
    justify-content: left;
    margin-right: 55px;
    margin-left: 10px;
  }