body {
    background-color:rgb(33, 33, 34);
}
/* for login/signup forms */
.form {
    display: inline-flex;
    justify-content: horizontal;
    align-items: center;
    text-align: center;
    border: solid 1px #ccc;
    width: 350px;
    height: 500px;
    top: 50%;
    left: 50%;
    position: center;
    margin: 0;
    margin-top: 250px;;
}
/* login/signup banner */
.login-h3 {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 24px;
    margin-bottom: 30px;
}
/* form inputs */
input[type=text], input[type=password] {
    width: 75%;
    padding: 12px 20px;
    padding-top: 10px;
    margin: 4px 85px;
    display: flex-direction;
    border: 1px solid rgba(115, 82, 122, 0.486);
    box-sizing: border-box;
    background-color: rgb(33, 33, 34);
    color:rgba(175, 149, 35, 0.836);
    font-size: 18px;
}
/* form text focux */
input[type=text]:focus, input[type=password]:focus {
    outline: none !important;
    border: 1px solid rgba(115, 82, 122, 0.938);
}
/* form text hover */
input[type=text]:hover, input[type=password]:hover {
    transform: scale(0.98);
}
/* for placeholder text in login form */
::placeholder {
    color: rgba(115, 82, 122, 0.938);
    display: flex;
    justify-content: center;
    text-align: center;
}
/* Set a style for all buttons */
button {
    display: flex;
    justify-content: center;
    flex-direction: horizontal;
    align-items: center;
    text-align: center;
    background-color: rgb(51, 47, 50);
    color: rgba(115, 82, 122, 1);
    /* height of button/width of button */
    padding: 15px 35px;
    /* spacing from "password"/spacing between buttons */
    margin: 20px 20px;
    border: none;
    cursor: pointer;
    width: 25%;
    font-size: 16px;
    /* for rounded corners on buttons */
    border-radius: 12px;
}
button:hover {
    opacity: 0.9;
    transform: scale(0.98);
}

/* div for login inputs */
.container1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100hv;
    margin: 0;
    width: 100%;
}
/* div for login/forgot buttons */
.container2 {
    display:flex;
    flex-direction: horizontal;
    justify-content: center;
}
/* div for login error message */
.container3 {
    display:flex;
    flex-direction: horizontal;
    justify-content: center;
}
/* div for div containing "Login/Signup" */
.container4 {
    display: inline-flex;
    flex-direction: horizontal;
    gap: 10px;
    text-align: center;
}
/* div for main flex container which holds login form/signup form */
.main-horizontal-container {
    display: flex;
    flex-direction: horizontal;
    align-items: center;
    margin: 0;
    justify-content: center;
    height: 100hv;
}

/* div containing "No Account? and button" */
.login-signup-toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

/* button on front page that toggles signup/login */
button.front-page-toggle {
    display: flex;
    font-size: 14px;
}